#targetingForm {
  width: 37%;
  overflow-y: auto;
  max-height: 100vh;
  float: left;
  margin-top: 10px;

}
.grey {
  color: red!important;
  text-align: center;
}

.form_description {
  padding: 0 16px 12px;
  font-size: 13px;
  line-height: 16px;
  color: $light-gray;
}

.age_from, .age_to {
  float: left;
  width: 45%;
}

.age_to {
  margin: 0 !important;
}

.clearfix .FormLayout__row-top {
  color: white !important;
}

