#targetingForm textarea {
  background-color: #2F304D;
  color: white;
  border: 1px solid #8e8fbb;
  border-radius: 6px;
}


.birthday_radio {
  margin: 0 !important;
  --accent: #FFC25E;
  float: left;
  width: 24%;

  .Radio__content {
    font-size: 14px;
    color: #9C9CA8
  }
}

.top_txt svg {
  width: 17px;
  height: 17px;
  margin-bottom: 10px;
  color: #9C9CA8!important;

}

.top_txt {
  display: flex;

  .icon {
    margin: 0 0 0 10px;

  }
}





